import Firebase from 'src/shared/utils/firebase-register';
import { GroupItem } from 'src/group/model/Group.model';
export class GroupService {
  createGroup(item: GroupItem): Promise<void> {
    return Firebase.db
      .collection('group')
      .add(item)
      .then(ref => ref.set({ id: ref.id }, { merge: true }));
  }

  updateGroup(item: GroupItem): Promise<void> {
    return Firebase.db
      .collection('group')
      .doc(item.id)
      .set(item, { merge: true });
  }

  deleteGroup(groupId: string): Promise<void> {
    return Firebase.db
      .collection('group')
      .doc(groupId)
      .delete();
  }

  getOwnerGroupList(userId: string): Promise<GroupItem[]> {
    return Firebase.db
      .collection('group')
      .where('stackholders', 'array-contains', userId)
      .get()
      .then(data => {
        const result: GroupItem[] = [];
        data.forEach(item => {
          const { id, name, stakeholders, managers } = item.data();
          result.push({
            id,
            name,
            stakeholders,
            managers
          });
        });
        return result;
      });
  }
}

export default new GroupService();
